<script>
  import { createEventDispatcher } from 'svelte';
  import {Stomp} from '../../scripts/stomp.js';
  import SockJS from '../../scripts/sockjs.js'; 

  const dispatch = createEventDispatcher(); 
  export let token = '';
  
  let message = '';
  let wsAddr = 'http://35.197.146.221/chat/ws';
  let username = document.getElementById('username').value;
  let recipientId = document.getElementById('recipient').value;
  let questId = document.getElementById('questid').value;
  let stompClient = null; 

  function setConnected(connected) {
    console.log(`connected: ${connected}`);
  }

  function connect() {
      let socket = new SockJS(wsAddr);
      stompClient = Stomp.over(socket);
      stompClient.connect({}, function(frame) {
          setConnected(true);
          console.log('Connected: ' + frame);
          stompClient.subscribe(`/user/${username}_${questId}/queue/messages`, function(resp){
              console.log("Message received");
              console.log(resp.body);
              dispatch('reload');
          });
      });
  }

  function disconnect() {
      stompClient.disconnect();
      setConnected(false);
      console.log("Disconnected");
  }

  function sendMessage() {
    let msg = message;
    if (msg !== null && msg !== undefined && msg !== '') {

      stompClient.send("/app/chat", {}, JSON.stringify({
        senderId: username,
        recipientId: recipientId,
        questId: questId,
        senderName: username,
        recipientName: recipientId,
        content: msg,
        timestamp: new Date(),
      }));
      message = '';
      dispatch('reload');
    }
  }

  setTimeout(() => {
    connect();
  }, 500);

</script>

<div class="input-group">
  <textarea class="uk-textarea" resize="none" bind:value={message} placeholder="enter your message"></textarea>
  <!-- <input type="text" class="uk-input" bind:value={message} placeholder="enter your message"/> -->
  <button class="uk-button-small uk-button-primary" on:click={sendMessage} disabled={message==''}>
      Send
  </button>
</div>
