<script>
	import '../node_modules/uikit/dist/css/uikit-core.min.css';
	import '../node_modules/uikit/dist/js/uikit';
	import ChatInput from './chat-room/ChatInput.svelte';
	import MessageList from './chat-room/MessageList.svelte';

	export const token = document.getElementById('token').value;
	export let list = [];
	const pullMessageAddr = 'https://questboard-api-gateway-pllorhkrga-as.a.run.app/api/chat-room-and-messages';
	let username = document.getElementById('username').value;
  let recipientId = document.getElementById('recipient').value;
  let questId = document.getElementById('questid').value;
	let MessageListComponent;

  async function pullNewMessage() {
		if (token != null) {
			const resp = await fetch(`${pullMessageAddr}`, {
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
					'Authorization': `Bearer ${token}`
				},
				body: JSON.stringify({
					"senderId": username,
					"recipientId": recipientId,
					"questId": questId
				})
			});
			
			const messages = await resp.json();
			console.log('messages recieved: ', messages);
			list = messages;
			MessageListComponent.showLatest();
		}
    console.log('pulled');
  }

	setTimeout(() => {
		pullNewMessage();
	}, 500);
</script>

<main>
	<MessageList bind:messageList={list} bind:this={MessageListComponent} {token}></MessageList>
	<ChatInput on:reload={pullNewMessage} {token}></ChatInput>
</main>

<style>
	main {
		/* padding: 1em; */
		font-family: -apple-system, BlinkMacSystemFont, sans-serif;
	}

	@media (min-width: 640px) {
		main {
			max-width: none;
		}
	}
</style>